import { selectPathname, selectProductById } from './selectors';

const analyticsMiddleware: AppMiddleware = ({ getState }) => (next) => (action) => {
    const label = action.type !== LOCATION_CHANGE ? action.type : 'LOCATION_CHANGE';
    const state = getState();
    const pathname = selectPathname(state);
    const currentScreen = pathname.substring(1);

    if (action.type === 'appContext/SET_APP_CONTEXT') {
        const lastDeviceId = analyticsInstance.getDeviceId();
        const newDeviceId = action.payload.device_id;

        if (newDeviceId !== lastDeviceId) {
            analyticsInstance.setDeviceId(newDeviceId);
        }
    }

    const frontendVersion = process.env.REACT_APP_REV || 'local';
    const stand = getStand();
    const opts = {
        properties: {
            // eslint-disable-next-line @typescript-eslint/camelcase
            screen_name: currentScreen,
            frontendVersion,
            stand,
        },
    };
    analyticsInstance.redux(label, action, opts);
    return next(action);
};

const serverActionMiddleware: AppMiddleware = ({ dispatch, getState }) => (next) => (action) => {
    switch (action.type) {
        case 'CLOSE_APP': {
            close();
            break;
        }
        case 'ASK_FOR_STOCK': {
            sendData({ action_id: action.type });

            break;
        }
        case 'ASK_FOR_CATEGORIES': {
            sendData({ action_id: action.type });

            break;
        }
        case 'ASK_FOR_ITEMS_CHANGES': {
            sendData({ action_id: action.type });

            break;
        }
        case 'ADDRESS_SUBMIT': {
            sendData({ action_id: action.type });

            break;
        }
        case 'ADDRESS_FULL_SUBMIT': {
            sendData({ action_id: action.type });

            break;
        }
        case 'ASK_FOR_ORDER_STATUS': {
            sendData({ action_id: action.type });

            break;
        }
        case 'ASK_FOR_ORDER_STATUS_FINAL': {
            sendData({ action_id: action.type });

            break;
        }
        case 'MAKE_PURCHASE': {
            sendData({ action_id: action.type });

            break;
        }
        case 'ASK_FOR_LAST_ORDER_INFO': {
            sendData({ action_id: action.type });

            break;
        }
        case 'PAGE_CHANGE': {
            sendData({ action_id: action.type });
            break;
        }
        case 'OPEN_PAYMENT': {
            sendData({ action_id: action.type });
            break;
        }
        /**
         * tldr;   LOCATION_CHANGE  dispatch'     setTimeout.
         *     action'  LOCATION_CHANGE,   
         *  connected-react-router  history.
         *
         * !           ,
         *      .
         *
         *  ,   ,    . ,   ,
         *      connected-react-router  history.
         * ,      .  
         * dispatch' push action react-connected-router'a   @@router/CALL_HISTORY_METHOD
         *   router middleware  
         *   (push)   history.
         *    history    History API 
         *    .   ,   .
         *  connected-react-router'  ,  
         *    (,  ) history.
         *  dispatch' action,   @@router/LOCATION_CHANGE,   
         *   connected-react-router   (    history)  router'a.
         *            react',
         *   connected-react-routera   ,    ,
         *    router'a      history.
         *     .        .
         *
         * -      connected-react-router'    action,
         *     (redux   ),   
         *    .    ,    -   history
         *      ,   router'a   ,  
         *     action.
         *       history,     
         *            .
         **/
        case LOCATION_CHANGE: {
            const state = getState();

            const currentPathname = selectPathname(state);
            const nextPathname = action.payload.location.pathname;

            /**
             *  LOCATION_CHANGE       
             *  .       
             * (       ,    ),
             *        .
             */
            if (currentPathname === nextPathname) {
                break;
            }

            switch (currentPathname) {
                case ROUTES.CATALOG:
                case ROUTES.CART: {
                    setTimeout(() => {
                        dispatch(applyItemsChanges());
                    });

                    break;
                }
                case ROUTES.ADDRESS_SIMPLE:
                case ROUTES.ADDRESS_SIMPLE_QUESTION: {
                    /**
                     *       
                     *       ,
                     *  ,       
                     * ,    ;)
                     */
                    if (nextPathname === ROUTES.ADDRESS_FULL_ALL) {
                        /**
                         *       ,    ,
                         *      ( ),    
                         *  (, , )   (    
                         *    ).
                         *         .
                         */
                        const isAddressChanged = state.userData.address !== state.userModel.address;
                        const hasValidAddress = getHasValidAddress(state.userModel);
                        if (!hasValidAddress || isAddressChanged) {
                            setTimeout(() => {
                                dispatch(revertUserChanges());
                            });
                        }
                    }

                    break;
                }
                case ROUTES.ADDRESS_FULL_ALL: {
                    if (nextPathname === ROUTES.CART) {
                        setTimeout(() => {
                            dispatch(revertUserChanges());
                        });
                    }
                    break;
                }

                case ROUTES.ERROR: {
                    setTimeout(() => {
                        dispatch(errorResetAction());
                    });

                    break;
                }

                default:
                    break;
            }

            switch (nextPathname) {
                case ROUTES.CATALOG: {
                    if (Object.keys(state.productsData.itemsMap).length) {
                        setTimeout(() => {
                            dispatch(askForItemsChangesAction());
                        });
                    }

                    break;
                }
                case ROUTES.CATEGORIES: {
                    if (Object.keys(state.categoriesData.itemsMap).length === 0) {
                        setTimeout(() => {
                            dispatch(askForCategoriesAction());
                        });
                    }

                    break;
                }
                case ROUTES.CART: {
                    if (process.env.REACT_APP_ENVIRONMENT === 'development') {
                        if (!Object.keys(state.productsData.itemsMap).length) {
                            setTimeout(() => {
                                dispatch(askForStockAction());
                            });
                        }
                    }

                    const hasChanges = Boolean(Object.keys(state.productsDiffData.itemsMap).length);
                    const shouldSkipAskChanges = currentPathname === ROUTES.WAIT && hasChanges;

                    if (shouldSkipAskChanges === false) {
                        setTimeout(() => {
                            dispatch(askForItemsChangesAction());
                        });
                    }

                    break;
                }

                case ROUTES.STATUS_SUMMARY: {
                    setTimeout(() => {
                        dispatch(askForLastOrderInfoAction());
                    });

                    break;
                }

                default:
                    break;
            }

            //     LOCATION_CHANGE,
            //         
            if (currentPathname !== nextPathname) {
                setTimeout(() => {
                    dispatch(pageChangeAction());
                });
            }

            break;
        }
        default:
            break;
    }

    return next(action);
};

const enhancers: StoreEnhancer[] = [];
const middlewares: Middleware[] = [
    thunk,
    routerMiddleware(history),
    serverActionMiddleware as Middleware,
    analyticsMiddleware as Middleware,
];

if (!isPROM) {
    if (typeof window === 'object' && window.__REDUX_DEVTOOLS_EXTENSION__) {
        enhancers.push(window.__REDUX_DEVTOOLS_EXTENSION__());
    }
}

export const store = createReduxStore<AppState, AppBaseAction, {}, {}>(
    getRootReducer(history, getRecoveryState()),
    compose(applyMiddleware(...middlewares), ...enhancers),
);
const dispatch = store.dispatch as AppDispatch;

if (!isPROM) {
    window.__store = store;
}

const dispatchSmartAppCommand = ({ smart_app_data }: SmartAppCommand) => {
    switch (smart_app_data.command) {
        case 'SHOW_ITEMS':
            dispatch(showItemsThunk(smart_app_data));
            break;
        case 'SHOW_CATEGORIES':
            dispatch(showCategoriesAction(smart_app_data));
            break;
        case 'ITEMS_CHANGES': {
            dispatch(itemsChangesAction(smart_app_data));
            break;
        }
        case 'ADD_ITEM': {
            dispatch(addItemAction(smart_app_data));
            break;
        }
        case 'REMOVE_ALL': {
            dispatch(removeAllAction());
            break;
        }
        case 'REMOVE_ITEM': {
            dispatch(removeItemAction({ ...smart_app_data, shouldRemoveMention: true }));
            break;
        }
        case 'GO_TO_CATALOG': {
            dispatch(goToCatalogThunk(smart_app_data));
            break;
        }
        case 'GO_TO_CATEGORIES': {
            dispatch(goToCategoriesThunk(smart_app_data));
            break;
        }
        case 'GO_TO_NO_AREA': {
            dispatch(goToNoAreaThunk(smart_app_data));
            break;
        }
        case 'GO_TO_VALIDATION': {
            dispatch(goToValidationThunk());
            break;
        }
        case 'PAYMENT_CANCELLED_BY_USER': {
            dispatch(paymentCancelledByUserThunk());
            break;
        }
        case 'GO_TO_PROMO': {
            dispatch(goToPromoThunk(smart_app_data));
            break;
        }
        case 'GO_TO_ADDRESS_SIMPLE': {
            dispatch(push(ROUTES.ADDRESS_SIMPLE));
            break;
        }
        case 'GO_TO_ADDRESS_FULL': {
            dispatch(goToAddressFullAllThunk());
            break;
        }
        case 'VALIDATION_OK': {
            dispatch(validationOkThunk(smart_app_data));
            break;
        }
        case 'ORDER_ERROR': {
            dispatch(orderErrorAction(smart_app_data));
            dispatch(replace(ROUTES.ERROR));
            break;
        }
        case 'ORDER_CHANGED': {
            dispatch(orderChangedAction(smart_app_data));
            dispatch(goBack());
            break;
        }
        case 'ADDRESS_GUIDE': {
            dispatch(addressGuideThunk());
            break;
        }
        case 'ADDRESS_INPUT': {
            dispatch(addressInputThunk(smart_app_data));
            break;
        }
        case 'ADDRESS_INPUT_ADD': {
            dispatch(addressInputAddThunk(smart_app_data));
            break;
        }
        case 'ADDRESS_INPUT_DECLINE': {
            dispatch(addressInputDeclineThunk());
            break;
        }
        case 'ADDRESS_INPUT_CLEAR': {
            dispatch(addressInputClearAction());
            break;
        }
        case 'ADDRESS_FULL_SUCCESS': {
            dispatch(addressFullSuccessThunk());
            break;
        }
        case 'ADDRESS_FULL_INPUT': {
            dispatch(addressFullInputThunk(smart_app_data));
            break;
        }
        case 'ADDRESS_FULL_INPUT_ADD': {
            dispatch(addressFullInputAddAction(smart_app_data));
            break;
        }
        case 'ADDRESS_FULL_INPUT_CLEAR': {
            dispatch(addressFullInputClearAction(smart_app_data));
            break;
        }
        case 'ADDRESS_FULL_COMMENT': {
            dispatch(addressFullCommentAction(smart_app_data));
            break;
        }
        case 'ADDRESS_FULL_VALIDATION_OK': {
            dispatch(addressFullValidationOkThunk());
            break;
        }
        case 'ADDRESS_FULL_VALIDATION_ERROR': {
            dispatch(addressFullValidationErrorAction());
            break;
        }
        case 'ASK_AGAIN_FOR_ORDER_STATUS_FINAL': {
            dispatch(askAgainForOrderStatusFinalAction());
            setTimeout(() => dispatch(askForOrderStatusFinalAction()), 500);
            break;
        }
        case 'GO_TO_WAIT': {
            dispatch(push(ROUTES.WAIT));
            dispatch(goToWaitAction());
            dispatch(askForOrderStatusAction());
            break;
        }
        case 'GO_BACK': {
            dispatch(goBackAction());
            dispatch(goBack());
            break;
        }
        case 'ASK_AGAIN_FOR_ORDER_STATUS': {
            dispatch(askAgainForOrderStatusAction());
            setTimeout(() => dispatch(askForOrderStatusAction()), 500);
            break;
        }
        case 'ORDER_SUCCESS': {
            dispatch(orderSuccessAction(smart_app_data));
            dispatch(openPaymentAction());
            break;
        }
        case 'GO_TO_ORDER_SEARCH': {
            dispatch(goBackToStartAndReplaceThunk(ROUTES.STATUS_SUMMARY));
            break;
        }
        case 'LAST_ORDER_INFO': {
            dispatch(lastOrderInfoAction(smart_app_data));
            break;
        }
        case 'LAST_ORDER_EMPTY': {
            dispatch(lastOrderEmptyAction());
            dispatch(goBackToStartAndReplaceThunk(ROUTES.STATUS_EMPTY));
            break;
        }
        case 'LAST_ORDER_ERROR': {
            dispatch(lastOrderErrorAction());
            dispatch(push(ROUTES.ERROR));
            break;
        }
        case 'GO_TO_LAST_ORDER_DETAILS': {
            dispatch(push(ROUTES.STATUS_DETAILS));
            break;
        }
        case 'GO_TO_LAST_ORDER_REPORT': {
            dispatch(push(ROUTES.STATUS_REPORT));
            break;
        }
        default:
            break;
    }
};

setOnData((command) => {
    switch (command.type) {
        case 'smart_app_data': {
            dispatchSmartAppCommand(command);
            break;
        }
        case 'character': {
            dispatch(setCharacterAction(command.character.id));
            break;
        }
        case 'app_context': {
            dispatch(setAppContextAction(command.app_context));
            break;
        }
        default:
            break;
    }
});

if (process.env.REACT_APP_ENVIRONMENT !== 'production' && module.hot) {
    module.hot.accept('./reducers', () => {
        // eslint-disable-next-line
        const { getRootReducer } = require('./reducers');
        const rootReducer = getRootReducer(history);

        store.replaceReducer(rootReducer);
    });
}

const castCatalogPortalItemSelectorFromStore = (state: AppState) => {
    const {
        catalogPagePortal: { order, screenIndexMap },
    } = state;

    return {
        ignored_words: ['', ''],
        items: order.map((itemId) => {
            const { name } = selectProductById(state, itemId);
            const number = screenIndexMap[itemId];

            return {
                id: itemId,
                title: name,
                number,
                visible: Boolean(number),
            };
        }),
    };
};

const castCatalogItemSelectorFromStore = (state: AppState): AppStateInterface['item_selector'] => {
    const {
        catalogPage: { order, screenIndexMap },
    } = state;

    return {
        ignored_words: ['', ''],
        items: order.map((itemId) => {
            const { name } = selectProductById(state, itemId);
            const number = screenIndexMap[itemId];

            return {
                id: itemId,
                title: name,
                number,
                visible: Boolean(number),
            };
        }),
    };
};

const castCartItemSelectorFromStore = (state: AppState): AppStateInterface['item_selector'] => {
    const {
        cart: { order },
    } = state;

    return {
        ignored_words: ['', ''],
        items: order.map((itemId, index) => {
            const { name } = selectProductById(state, itemId);

            return {
                id: itemId,
                title: name,
                number: index + 1,
                visible: false,
            };
        }),
    };
};

const castDetailsItemSelectorFromStore = (state: AppState): AppStateInterface['item_selector'] => {
    const {
        itemDetails: { id },
        productsData: { itemsMap },
    } = state;

    const item = itemsMap[id];
    return {
        ignored_words: ['', ''],
        items: [
            {
                id,
                title: item.name,
                visible: true,
            },
        ],
    };
};

const castItemSelectorFromStore = (state: AppState): AppStateInterface['item_selector'] => {
    const pathname = selectPathname(state);

    switch (pathname) {
        case ROUTES.CATALOG: {
            if (deviceFamily === DeviceFamily.PORTAL) {
                return castCatalogPortalItemSelectorFromStore(state);
            }

            return castCatalogItemSelectorFromStore(state);
        }

        case ROUTES.CART:
            return castCartItemSelectorFromStore(state);

        case ROUTES.ITEM_DETAILS:
            return castDetailsItemSelectorFromStore(state);

        default:
            return {
                ignored_words: [],
                items: [],
            };
    }
};

const serializeAddress = (storeUser: StoreUser): ScenarioUserAddress => {
    const {
        address,
        city,
        street,
        house,
        building,
        floor,
        entrance,
        apartment,
        doorCode,
        addressLocationLatitude,
        addressLocationLongitude,
    } = storeUser;

    return {
        location: {
            latitude: addressLocationLatitude,
            longitude: addressLocationLongitude,
        },
        address_string: address,
        city,
        street,
        house,
        building,
        floor,
        entrance,
        apartment,
        door_code: doorCode,
        confirmed: 'true',
    };
};

const prepareAddress = (state: AppState): ScenarioUser => {
    let source;

    switch (selectPathname(state)) {
        case ROUTES.ADDRESS_SIMPLE:
        case ROUTES.ADDRESS_SIMPLE_QUESTION:
        case ROUTES.ADDRESS_FULL_ALL: {
            source = state.userModel;

            break;
        }

        default: {
            source = state.userData;

            break;
        }
    }

    return {
        phone_num: source.phoneNumber,
        customer_name: source.customerName,
        address: serializeAddress(source),
    };
};

setGetState(() => {
    const state = store.getState();
    const { cart } = state;
    const pathname = selectPathname(state);

    return {
        item_selector: castItemSelectorFromStore(state),
        foodtech: {
            currentScreen: pathname.substring(1),
            products: Object.keys(cart.itemsMap)
                .map((id) => {
                    const quantity = getCountById(state, id);

                    return { id, quantity };
                })
                .filter(({ quantity }) => Boolean(quantity)),
            totalPrice: {
                value: cart.totalPrice,
                discountValue: cart.totalPrice,
            },
            ...prepareAddress(state),
        },
    };
});

setGetRecoveryState(() => store.getState());
